.input-data {
  display: block;

  font-size: $font-size-m + 2px;
  line-height: 20px;
  color: $light-purple;
}

.input-data__field {
  width: 100%;
  padding-right: 10px;
  padding-bottom: 8px;

  font: inherit;

  background-color: transparent;
  border: none;
  border-bottom: 2px solid $main-text;

  &::placeholder {
    color: $light-purple;
  }

  &:hover,
  &:focus {
    border-bottom-color: $second-text;
    outline: none;
  }
}

@media (min-width: $tablet-width) {
  .input-data__field {
    padding-bottom: 5px;
  }
}
