.directions {
  z-index: -1;

  display: flex;
  flex-direction: column;
  padding: 0 $mobile-indent 7px;

  background-repeat: no-repeat;
  background-position: left 145px;
}

.no-webp .lazy.directions {
  background-image: none;
}

.webp .lazy.directions {
  background-image: none;
}

.no-webp .directions {
  background-image: url("../img/bg-yoga-patterns-mobile@1x.png");

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-yoga-patterns-mobile@2x.png");
    background-size: 216px 494px;
  }
}

.webp .directions {
  background-image: url("../img/bg-yoga-patterns-mobile@1x.webp");

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-yoga-patterns-mobile@2x.webp");
    background-size: 216px 494px;
  }
}

.directions__title {
  margin: 0 0 38px;
}

.directions__list {
  @include reset-list;
}

.directions__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  box-shadow: 0 4px 25px rgba(142, 128, 169, 0.2);
}

.directions__img {
  display: block;

  background-color: rgb(78, 78, 78);

  object-fit: cover;
}

.directions__info-wrapper {
  position: relative;

  width: 100%;
  padding: 16px 20px 15px;

  background-color: $white;
}

.directions__name {
  margin: 0 0 5px;

  font-weight: bold;
  font-size: $font-size-m;
}

.directions__days {
  margin: 0;

  &:last-of-type {
    margin-bottom: 4px;
  }
}

.directions__instructor {
  color: $second-text;
}

.directions__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;

  width: 17%;
}

.directions__link-more {
  display: none;
}

@media (min-width: $tablet-width) {
  .directions {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 $tablet-indent - 11px 120px $tablet-indent;

    background-position: left -20px;
  }

  .no-webp .directions {
    background-image: url("../img/bg-yoga-patterns-tablet@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-yoga-patterns-tablet@2x.png");
      background-size: 693px 397px;
    }
  }

  .webp .directions {
    background-image: url("../img/bg-yoga-patterns-tablet@1x.webp");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-yoga-patterns-tablet@2x.webp");
      background-size: 693px 397px;
    }
  }

  .directions__title {
    flex-basis: 100%;
    margin-bottom: 40px;
  }

  .directions__list {
    display: flex;
    flex-basis: 57%;
    flex-direction: column;
  }

  .directions__item {
    position: relative;

    flex-direction: row;
    flex-wrap: nowrap;
    margin-bottom: 19px;

    &--main {
      flex-basis: 39.2%;
      flex-direction: column;
      margin-bottom: 0;

      background-color: $white;
    }

    &:nth-last-child(2):not(.directions__item--main) {
      margin-bottom: 35px;
    }

    &:last-child {
      margin-top: auto;
      margin-bottom: 0;
    }
  }

  .directions__picture-wrapper {
    position: relative;

    width: 42%;
    padding-bottom: 34%;
    overflow: hidden;
  }

  .directions__img {
    position: absolute;
    top: 50%;
    left: 50%;

    width: auto;
    min-height: 100%;

    transform: translate(-50%, -50%);

    object-fit: fill;

    &--main {
      position: static;
      top: auto;
      left: auto;

      width: 100%;
      height: auto;

      transform: none;
    }
  }
  .directions__info-wrapper {
    flex-grow: 1;
    width: 58%;
    padding: 19px 45px 17px 19px;

    &--main {
      width: 100%;
      height: 100%;
    }
  }

  .directions__name {
    margin-bottom: 4px;
  }

  .directions__days {
    &:last-of-type {
      margin-bottom: 7px;
    }
  }

  .directions__link {
    position: absolute;
    top: auto;
    right: 24px;
    bottom: 27px;

    width: 30px;

    &--main {
      top: 0;
      right: 0;
      bottom: 0;

      width: 17%;
    }
  }

  .directions__link-more {
    display: block;
    flex-basis: 100%;
    padding: 10px 9px 7px 0;

    text-align: center;
    color: $main-text;

    background-color: $white;
    box-shadow: 0 4px 25px rgba(142, 128, 169, 0.2);
  }
}

@media (min-width: $desktop-width) {
  .directions {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 $desktop-indent 125px $desktop-indent - 35px;

    background-position: 58% -61px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {

    }
  }

  .no-webp .directions {
    background-image: url("../img/bg-yoga-patterns-desktop@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-yoga-patterns-desktop@2x.png");
      background-size: 1257px 722px;
    }
  }

  .webp .directions {
    background-image: url("../img/bg-yoga-patterns-desktop@1x.webp");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-yoga-patterns-desktop@2x.webp");
      background-size: 1257px 722px;
    }
  }

  .directions__title {
    margin-bottom: 67px;
    margin-left: 30px;
  }

  .directions__list {
    flex-basis: 56.8%;
  }

  .directions__item {
    margin-bottom: 28px;

    &:nth-last-child(2):not(.directions__item--main) {
      margin-bottom: 57px;
    }

    &:last-child {
      margin-top: 0;
      margin-bottom: 0;
    }

    &--main {
      flex-basis: 39.5%;
    }
  }

  .directions__info-wrapper {
    width: 305px;
    padding: 33px 80px 32px 35px;

    &--main {
      width: 100%;
      padding: 42px 46px 25px;
    }
  }

  .directions__name {
    margin-bottom: 13px;

    font-size: $font-size-l;
  }

  .directions__days {
    font-size: $font-size-m;
    line-height: 20px;

    &:last-of-type {
      margin-bottom: 15px;
    }
  }

  .directions__instructor {
    font-size: $font-size-m;

    transition: color 0.3s ease-out, text-shadow 0.3s ease-out;

    &:hover,
    &:focus {
      color: $dark-purple;
      text-shadow: 0 3px 4px $main-text;
    }

    &:active {
      color: $second-text;
    }
  }

  .directions__link {
    right: 36px;
    bottom: 37px;

    width: 45px;

    &--main {
      top: 0;
      right: 0;
      bottom: 0;

      width: 17%;
    }
  }

  .directions__link-more {
    padding: 16px;

    font-size: $font-size-m;

    transition: color 0.3s ease-out, text-shadow 0.3s ease-out, box-shadow 0.3s ease-out;

    &:hover,
    &:focus {
      color: $dark-purple;
      text-shadow: 0 3px 4px $main-text;

      box-shadow: 0 10px 9px rgba(30, 30, 44, 0.5);
    }

    &:active {
      color: $second-text;
    }
  }
}
